<template>
  <div class="container">
    <div v-for="(delay, index) in delays" :key="index" :style="{ 'animation-delay': delay + 's' }" class="box"></div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      delays: [0, 0.5, 1] // Delays para cada box
    };
  }
};
</script>

<style scoped>
.container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 10vh;
}

.box {
  width: 10px;
  height: 10px;
  border-radius: 100%;
  background-color: #c25042;
  margin: 10px;
  animation: moveUpDown 1.5s infinite;
}

@keyframes moveUpDown {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(20px);
  }
}
</style>
