<template>
  <div class="flex flex-col justify-center items-center align-center w-screen h-screen">
    <h1 class="text-3xl font-bold text-slate-50">redirecionando</h1>
    <div>
      <AnimatedDots />
    </div>
    <span id="notify"></span>
  </div>
</template>

<script>
import AnimatedDots from "./components/AnimatedDots.vue";
import axios from "axios";
import $ from 'jquery';

export default {
  name: 'App',
  components: {
    AnimatedDots
  },
  mounted() {
    $("span#notify").text("")
    setTimeout(() => {
      this.redirect();
    }, 2000)
  },
  methods: {
    getParameterByName(name, url) {
      if (!url) url = window.location.href;
      name = name.replace(/[[\]]/g, '$&');
      var regex = new RegExp('[?&]' + name + '=([^&#]*)'),
        results = regex.exec(url);
      if (!results) return null;
      return decodeURIComponent(results[1].replace(/\+/g, ' '));
    },


    async redirect() {
      const target = this.getParameterByName('target');
      const authenticated = await this.verifyToken();

      let errorMessage = "Erro de direcionamento, entre em contato com nosso suporte.\n\nErro: 4000";

      if (!authenticated) {
        errorMessage = "Erro de direcionamento, entre em contato com nosso suporte.\n\nErro: 4001";
      } else if (!target) {
        errorMessage = "Erro de direcionamento, entre em contato com nosso suporte.\n\nErro: 4002";
      } else {
        window.location.href = target;
        if (!target.startsWith("https:") || !target.startsWith("http:")) {
          setTimeout(() => {
            $("span#notify").text("Você será redirecionado em breve. Esta janela pode ser fechada.")
          }, 3000);
        }
        return;
      }

      $("span#notify").text(errorMessage);
      setTimeout(() => {
        window.location.href = 'https://santiagotec.com.br';
      },3000)
    },

    async verifyToken() {
      const token = this.getParameterByName('token')
      try {
        const res = await axios.post(`https://wb.santiagotec.com.br/verify-token/${token}`)
        return res.status == 200
      } catch (error) {
        return false
      }
    }
  }
}
</script>
